@use '../../../assets/sass' as *;

.popular {
  padding-top: 3rem;
  padding-bottom: 5rem;
  background: lighten($light, 3.5%);

  &__head {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0;
    margin-bottom: 3rem;
  }

  &__body {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
  }
}